.modalBackground {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.377);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-container {
  width: 25em;
  height: 10em;
  border-radius: 1rem;
  background-color: #555555;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.modal-content {
  width: 100%;
  display: flex;
  justify-content: center;
}
.newFile{
  height: 13em;
}
.buttonNewFile{
  background-color: #007298;
  color: white;
}
.buttonNewFile:hover {
  background-color: white;
  color: #007298;
  border: 2px solid #007298;
  font-weight: bold;
}
